// FLEXBOX
@mixin centered-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin row-spaced-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

// MEDIA QUERIES
@mixin respond($breakpoint) {
  @media only screen and (max-width: $breakpoint) {
    @content;
  }
}
