.hidden {
  display: none;
}

.underline-animation {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: all 0.5s;
}
.underline-animation:before {
  content: "";
  position: absolute;
  bottom: -0.75rem;
  left: 0;
  width: 0;
  height: 1.5px;
  transition: all 0.4s;
  background-color: #4c2771;
}
.underline-animation:hover:before {
  width: 100%;
}

@media only screen and (max-width: 75em) {
  .hero {
    display: none;
  }
}
.hero__brand {
  z-index: 100;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.hero__img {
  z-index: 100;
  width: 30rem;
}
.hero__text {
