  color: #fff;
}
.hero__text p {
  text-transform: uppercase;
  text-shadow: 2px 2px 0 rgba(76, 39, 113, 0.5);
}

.wrapper {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 45%;
  background-image: linear-gradient(to top, rgb(30, 11, 66) 0%, rgb(118, 77, 194) 100%);
