  height: 1025px;
  border-radius: 35%;
  background: rgba(255, 255, 255, 0.9);
  animation: rotate-right 15s infinite linear;
}
.wave--2 {
  z-index: 10;
  top: 6%;
  width: 1050px;
  height: 1040px;
  border-radius: 40%;
  background: rgba(255, 255, 255, 0.1);
  animation: rotate-left 10s infinite linear;
}

.button {
  cursor: pointer;
  border: none;
  font-size: 1.6rem;
  transition: all 0.2s;
  border-radius: 3px;
  padding: 1rem 1.5rem;
}
.button:hover {
