}
.button--underline:hover {
  text-decoration: none;
}

.card {
  position: relative;
  background-color: #fff;
  border-radius: 3px;
  border-left: 5px solid transparent;
  padding: 0.5rem;
  margin-bottom: 1rem;
  min-height: 8rem;
}
.card.trivial {
  border-color: #02afff;
}
.card.easy {
  border-color: #2eaf2e;
}
.card.medium {
  border-color: #ff7b00;
}
.card.hard {
  border-color: #dc143c;
}
.card.epic {
  border-color: #000;
}
.card__title {
  font-weight: 500;
  font-size: 2rem;
}
.card__difficulty {
  font-size: 1.2rem;
  font-weight: 500;
  text-transform: uppercase;
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  padding: 0.25rem;
  color: #fff;
  border-radius: 3px;
}
.card__difficulty.trivial {
  background-color: #02afff;
}
.card__difficulty.easy {
  background-color: #2eaf2e;
}
.card__difficulty.medium {
  background-color: #ff7b00;
}
.card__difficulty.hard {
  background-color: #dc143c;
}
.card__difficulty.epic {
  background-color: #000;
}
.card__members {
  position: relative;
  display: flex;
  gap: 0.5rem;
  padding-left: 0.5rem;
  margin: 3rem 0;
}
.card__member {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: all 0.2s;
  height: 3rem;
  width: 3rem;
  color: #947daa;
  border: 1px solid #947daa;
  border-radius: 50%;
}
.card__member--author {
  border-color: #a57d18;
  color: #a57d18;
}
.card__member--author i {
  font-size: 1.2rem;
  position: absolute;
  top: -0.7rem;
  left: -0.7rem;
  transform: rotate(-40deg);
  z-index: 5;
}
.card__member--author span {
  background-color: #a57d18 !important;
}
.card__member span {
  z-index: 300;
  display: none;
  padding: 0.5rem;
  border-radius: 3px;
  background-color: #4c2771;
  color: #fff;
}
.card__member:hover {
  background-color: rgba(76, 39, 113, 0.1);
}
.card__member:hover span {
  display: inline-block;
  position: absolute;
  top: 3rem;
}
.card__member i {
  position: absolute;
  font-size: 1rem;
}
.card__left-members {
  color: #696969;
  border-color: #696969;
}
.card__left-members p {
  padding-right: 0.4rem;
  padding-top: 0.1rem;
}
.card__time {
  position: absolute;
  top: 0.5rem;
  right: 3rem;
  display: flex;
  gap: 0.25rem;
  background-color: #2e4550;
  color: #fff;
  padding: 0.5rem;
  border-radius: 3px;
  font-size: 1.2rem;
}
.card__time i {
  margin-right: 0.25rem;
}
.card__date {
  color: #696969;
  font-size: 1.2rem;
  padding: 0.75rem;
}
.card__btn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background-color: transparent;
  color: #232323;
  font-size: 2rem;
  cursor: pointer;
}

.backdrop {
  z-index: 250;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(2px);
}

.filter {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding: 0.5rem 2rem;
  margin-bottom: 1.5rem;
  background-color: rgba(76, 39, 113, 0.4);
  border-radius: 3px;
}
