}
.tasks.background-2 {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../assets/tasks-background-2.jpg");
}
.tasks.background-3 {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../assets/tasks-background-3.jpg");
}
.tasks.background-4 {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("../assets/tasks-background-4.jpg");
}
.tasks.background-5 {
