.modal {
  z-index: 300;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-left: 10px solid transparent;
  height: 45rem;
  width: 80rem;
  padding: 1.5rem;
  border-radius: 3px;
  background-color: #fff;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 2fr 2fr 2fr 3fr 5fr 1fr;
  column-gap: 2rem;
}
.modal.trivial {
  border-color: #02afff;
}
.modal.easy {
  border-color: #2eaf2e;
}
.modal.medium {
  border-color: #ff7b00;
}
.modal.hard {
  border-color: #dc143c;
}
.modal.epic {
  border-color: #000;
}

.modal-title {
