.error404__link:hover {
  background-color: transparent;
  color: #4c2771;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 200;
  background-color: #f2f2f2;
  position: fixed;
  width: 100%;
  height: 7rem;
  padding: 0 3rem;
  border-bottom: 3px solid #4c2771;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header__logo {
  max-height: 7rem;
}
.header__list {
  display: flex;
  justify-content: space-evenly;
  flex-basis: 25%;
}
.header__list-item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__list-item p {
  font-weight: 500;
  font-size: 2rem;
}
.header__list-item p, .header__list-item a:link, .header__list-item a:visited {
  color: #4c2771;
}
.header__user {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #4c2771;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  transition: all 0.2s;
}
.header__user:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.header__nav, .header__list, .header__list-item, .header__link {
  height: 100%;
}

.sidebar,
.sidebar-opener {
  position: absolute;
  top: 7rem;
  left: 0;
  height: calc(100vh - 7rem);
}

.sidebar {
  z-index: 100;
  overflow: hidden;
  width: 40rem;
  background-color: rgba(38, 20, 57, 0.8);
  backdrop-filter: blur(3px);
  transform: translateX(-40rem);
  transition: transform 0.4s;
}
.sidebar__heading {
  color: #fff;
  margin-left: 2rem;
}
.sidebar__opened {
  transform: translate(0);
}
.sidebar__close-btn {
  right: 1rem;
  border-radius: 3px;
  background-color: transparent;
}
.sidebar__close-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.sidebar-opener {
  z-index: 120;
  border-right: 1px solid rgba(153, 153, 153, 0.2);
  background-color: rgba(255, 255, 255, 0.1);
  width: 2rem;
  transition: all 0.3s;
}
.sidebar-opener__closed {
  width: 0px;
  border-color: transparent;
}
.sidebar-opener:hover {
  background-color: rgba(255, 255, 255, 0.2);
  cursor: pointer;
}
.sidebar-opener:hover button {
  background-color: rgba(130, 104, 156, 0.6);
}
.sidebar-opener__btn {
  right: -1.5rem;
  background-color: rgba(76, 39, 113, 0.5);
  backdrop-filter: blur(1px);
  border-radius: 50%;
}

.sidebar__close-btn,
.sidebar-opener__btn {
  position: absolute;
  top: 3rem;
  font-size: 2rem;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  color: #fff;
}

.login,
.register {
  display: grid;
  grid-template-columns: 55% 45%;
  height: 100vh;
}
@media only screen and (max-width: 75em) {
  .login,
.register {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.login__content,
.register__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.login__cta,
.register__cta {
  display: flex;
  justify-content: space-between;
  gap: 10rem;
}

.login__form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-size: 2rem;
}

.register__form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 4rem;
  row-gap: 2rem;
  position: relative;
}
@media only screen and (max-height: 25em) {
  .register__form {
    row-gap: 1rem;
  }
}
.register__box.disabled {
  filter: grayscale(100%);
  opacity: 0.4;
}
.register__box--5 {
  z-index: 10;
}
.register__box--7 {
  transition: opacity 0.3s, transform 0.8s;
  opacity: 1;
  transform: translateY(-1.5rem);
}
.register__box--7.hidden-input {
  z-index: -10;
  opacity: 0;
  transform: translateY(-9rem);
  pointer-events: none !important;
}
.register__box--8 {
  grid-column: 1/-1;
}
.register__box--8 span {
  font-size: 1.6rem;
}
.register__box--9 {
  grid-column: 1/-1;
}
.register__box--9 input {
  width: 100%;
}
.register__box--10 {
  grid-column: 1/-1;
  justify-self: center;
  display: flex;
  gap: 5rem;
}
.register__box--10 > * {
  width: 12rem;
}

.input-group {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  background-color: #4c2771;
  border: 2px solid #4c2771;
  border-radius: 3px;
  transition: all 0.3s;
}
.input-group label {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 6rem;
}
.input-group textarea {
  color: #696969;
  font-size: 1.6rem;
  font-family: inherit;
  padding: 1rem;
  width: 30rem;
}
.input-group input, .input-group select {
  font-size: 1.6rem;
  background-color: #fff;
  clip-path: polygon(5% 0, 0 100%, 100% 100%, 100% 0);
  border: none;
  padding: 1rem;
  width: 30rem;
  text-align: center;
}
.input-group select {
  cursor: pointer;
}
.input-group__dropdown {
  position: relative;
}
.input-group__dropdown--i {
  position: absolute;
  right: 1rem;
  pointer-events: none;
  background: none !important;
  color: #4c2771;
}

.user {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../assets/background.webp");
  background-size: cover;
  background-position: bottom;
}
.user__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  height: 50rem;
  width: 80rem;
  padding: 3rem;
  border-radius: 3px;
}
.user__title {
  font-size: 3rem;
  font-weight: 200;
}
.user__picture-box {
  background-color: #4c2771;
  padding: 1rem;
}
.user__picture {
  border: 2px solid #fff;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 3rem;
  width: 5rem;
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.user__text {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 2rem;
}
.user__paragraph {
  display: flex;
  align-items: center;
  position: relative;
}
.user__paragraph--1 {
  text-align: center;
  position: absolute;
  top: -0.8rem;
  left: 2rem;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: #4c2771;
  background-color: #fff;
  padding: 0 0.5rem;
}
.user__paragraph--2 {
  border: 1px solid #4c2771;
  border-radius: 3px;
  padding: 0.5rem 1rem;
  width: 30rem;
  overflow-x: auto;
}
.user__team-id {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  gap: 3rem;
  background-color: #4c2771;
  color: #fff;
  border-radius: 3px;
  padding: 1rem;
}
.user__team-id code {
  padding: 0.5rem 5rem;
  background-color: #fff;
  color: #4c2771;
}
.user__team-id i {
  cursor: pointer;
}
.user__copy-message {
  position: absolute;
  top: 0;
  right: 2rem;
  font-size: 1.2rem;
  color: #fff;
  background-color: #3f3f3f;
  border-radius: 3px;
  padding: 1rem 0.5rem;
  animation: move-up 2s linear;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  -ms-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
.user__home, .user__settings {
  cursor: pointer;
  position: absolute;
  color: #4c2771;
  top: 4rem;
  font-size: 2rem;
}
.user__home::before, .user__settings::before {
  font-size: 1.6rem;
  display: none;
  background-color: #fff;
  color: #4c2771;
  padding: 0.25rem 0.5rem;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.user__home:hover::before, .user__settings:hover::before {
  display: block;
  position: absolute;
  top: 3rem;
}
.user__home {
  right: 7rem;
  transition: all 0.2s;
}
.user__home:hover {
  transform: translateY(-2px);
}
.user__home::before {
  content: "Home";
}
.user__settings {
  right: 3rem;
}
.user__settings:hover i {
  animation: rotate-right 2s infinite linear;
}
.user__settings::before {
  content: "Settings";
}

