body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: $medium-font-size;
}

.heading--primary {
  font-family: "Allerta Stencil", sans-serif;
  font-size: 10rem;
  font-weight: 100;
  color: $color-white;
  text-shadow: 1rem 1rem 0 rgb($color-primary, 0.5);
}

.heading--secondary {
  font-size: 4rem;
  font-weight: 200;
  color: tint($color-primary, 30%);

  @media only screen and (max-height: 25em) {
    margin-bottom: 2rem;
  }
}

.heading--tertiary {
  font-weight: 400;
  color: $color-white;

  display: flex;
  align-items: center;
  gap: 1rem;
}

.swal {
  font-size: $big-font-size !important;
}
